@font-face {
  font-family: "ubisoft_sansbold";
  src: url("ubisoftsans-bold.woff2") format("woff2"),
    url("ubisoftsans-bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "ubisoft_sansregular";
  src: url("ubisoftsans-regular.woff2") format("woff2"),
    url("ubisoftsans-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
* {
  font-family: "ubisoft_sansregular", sans-serif;
}
.product-slideshow {
  display: flex;
  justify-content: center;
}
.single-product {
  margin: 10px;
}
img.product-img {
  width: 249px;
  height: auto;
}

.product-title {
  font-family: "ubisoft_sansbold", sans-serif;
  font-weight: 600;
  font-size: 22px;
  color: #000;
  min-height: 60px;
  margin-top: 5px;
  max-width: 249px;
}
.product-date {
  color: #999;
  text-transform: uppercase;
  font-size: 10px;
}
.product-stores i {
  color: #999;
  width: 18px;
  height: 15px;
  margin: 3px;
}

.icon-xbox {
  content: url("assets/xbox.svg");
}

.icon-ps4 {
  content: url("assets/ps4.svg");
}

.icon-win {
  content: url("assets/windows.svg");
}
.icon-stadia {
  content: url("assets/stadia.svg");
}

body {
  margin: 0px;
}

h1 {
  font-family: "ubisoft_sansbold", sans-serif;
  text-align: center;
  text-transform: uppercase;
  font-weight: 600;
}
svg {
  fill: #ffffff;
  width: 93px;
  margin: 5px 30px 5px 30px;
}
.Nav {
  display: inline-flex;
  width: 100%;
}
.navbar {
  width: 800px;
  margin: 0 auto;
  clear: none;
  display: block;
}
.nav-item {
  color: #ffffff;
  font-family: "ubisoft_sansbold", sans-serif;
  text-transform: uppercase;
  vertical-align: center;
  display: inline-flex;
  padding: 15px 30px;
}
a {
  text-decoration: none;
}
.Nav {
  background-color: #000;
}

button {
  font-family: "ubisoft_sansbold", sans-serif;
  padding: 15px;
  width: 200px;
  border-radius: 50px;
  text-transform: uppercase;
  font-weight: 600;
  background-color: rgb(241, 59, 108);
  color: #fff;
  border: none;
  margin: 0 auto;
  text-align: center;
  display: block;
}
.btn-link {
  margin: 0 auto;
  text-align: center;
}
.Explore {
  background-color: #eff5f3;
  padding: 30px;
}
.Videos {
  background-color: #eff5f3;
  padding: 30px;
  height: 500px;
  vertical-align: middle;
  margin: 0 auto;
}
